import SfLink from "../SfLink/SfLink";
export default {
  name: "SfBreadcrumbs",
  components: {
    SfLink,
  },
  props: {
    /**
     * List of breadcrumbs (array of nested objects: `[ { text, route } ]`)
     */
    breadcrumbs: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    last() {
      return this.breadcrumbs.length - 1;
    },
  },
};
